@use "sass:map";

.contact {
  padding: 15vh 5vw 8vh 5vw;
  font-family: $font-text;

  .for-centering {
    text-align: center
  }

  &_heading {
    @include heading;
  }

  &_information {
    display: flex;
    justify-content: space-between;
    padding-top: 10vh;

    .alignment {
      padding-top: 1.5vh;
    }

    &--name {
      @include heading;
      font-size: 1.5vw;
      padding-bottom: 1vh;
      font-weight: 400;
      font-family: $font-text;
      border: none;
    }

    &--nip {
      font-size: 1.3vw;
      padding-bottom: 10vh;
      font-weight: 300;
      text-decoration: none;
      -webkit-text-decoration: none;
    }

    &--details {
      display: flex;
      align-items: center;
      padding-bottom: 2.3vh;
      font-family: $font-text;
      font-weight: 300;
    }

    &--mail {
      font-size: 1.3vw;
      margin-left: 1.5rem;
      text-decoration: none;
      color: map.get($colors, font-color);
    }

    &--link {
      text-decoration: none;
      font-size: 1.3vw;
      color: map.get($colors, accent-color);
      border-bottom: 3px solid transparent;
      padding-top: 3px;
      margin-left: 1.5rem;
      font-weight: 300;

      &:hover {
        font-weight: 500;
      }

      &:active {
        font-weight: 100;
      }
    }

    .icon {
      height: 6vh;
    }

    .phone-numbers {
      margin-left: 1.5rem;
      
      &_number {
        text-decoration: none;
        color: map.get($colors, font-color);
      }
    }
  }
}

@media (max-width: 792px) {
  .contact {
    padding: 12vh 5vw 8vh 5vw;
    font-family: $font-text;
  
    &_heading {
      @include mobileHeading;
      text-align: center;
    }
  
    &_information {
      flex-direction: column;
      align-items: center;
      margin: 0;
      text-align: center;
      padding-top: 8vh;
  
      .alignment {
        padding-top: 0;
      }
  
      &--name {
        font-size: 5vw;
        padding-bottom: 1vh;
        font-weight: 400;
        text-decoration: none;
        font-family: $font-text;
      }
  
      &--nip {
        font-size: 4vw;
        padding-bottom: 10vh;
        font-weight: 300;
      }
  
      &--details {
        flex-direction: column;
        padding-bottom: 7vh;
      }
  
      &--mail {
        font-size: 4vw;
        margin-left: 0;
      }
  
      &--link {
        text-decoration: none;
        font-size: 4vw;
        color: map.get($colors, accent-color);
        border-bottom: 3px solid transparent;
        padding-top: 3px;
        margin-left: 0;
        font-weight: 300;
  
        &:hover {
          font-weight: 500;
        }
  
        &:active {
          font-weight: 100;
        }
      }
  
      .icon {
        height: 7vh;
        margin-bottom: 2vh;
      }
  
      .phone-numbers {
        margin-left: 0;
        display: flex;
        flex-direction: column;
        font-size: 4vw;
      }

      .span {
        display: none;
      }
    }
  }
}

@media (min-width: 792px) and (max-width: 1100px) {
  .contact {
    &_information {
      margin: 0 3rem;

      &--name {
        font-size: 3rem;
        line-height: 2.8rem;
      }

      &--nip {
        font-size: 1.4rem;
      }

      &--details {
        padding-bottom: 2rem;
      }

      &--paragraph {
        font-size: 1.3rem;
      }

      .icon {
        font-size: 1.8rem;
      }
    }
  }
}